.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.border-gradiant {
  border-width: 5px;
  border-style: solid;
  border-image: linear-gradient(180deg, transparent 0%, #FE6B8B 30%, #FF8E53 90%, transparent 100%) 2 100%;
}

.border-gradiant-edit {
  border-width: 5px;
  border-style: solid;
  border-image: linear-gradient(180deg, transparent 0%, #6bfe9c 30%, #cbff53 90%, transparent 100%) 2 100%;
}